* {
  box-sizing: border-box;
}

body {
  font-family: "Stratos LC", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 135%;
  min-width: 320px;
  min-height: 100vh;
  max-width: 100vw;
  height: 600vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.uppercase, .primary-button {
  text-transform: uppercase;
}

button, a {
  cursor: pointer;
}

.line-clamp {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  box-orient: vertical;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.non-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.non-scrollbar::-webkit-scrollbar {
  display: none;
}

.standart-padding-both {
  padding: 0 100px;
}
@media screen and (max-width: 1440px) {
  .standart-padding-both {
    padding: 0 calc((100vw - 1024px) / 8.32 + 50px);
  }
}
@media screen and (max-width: 1024px) {
  .standart-padding-both {
    padding: 0 calc((100vw - 768px) / 22.4 + 30px);
  }
}
@media screen and (max-width: 768px) {
  .standart-padding-both {
    padding: 0 20px;
  }
}

.navigation {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
  width: 100%;
}
.navigation__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  padding-top: 19px !important;
  z-index: 100;
  position: sticky;
  top: 0;
  max-width: 1240px;
}
@media screen and (max-width: 620px) {
  .navigation {
    justify-content: flex-start;
  }
}
.navigation__menu {
  width: max-content;
  height: min-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  box-sizing: content-box;
  margin-top: 30px;
  overflow: hidden;
  border-radius: 2px;
}
@media screen and (max-width: 620px) {
  .navigation__menu {
    align-items: flex-start;
  }
}
.navigation__menu__button {
